#about {
    img.main {
        margin-bottom: 20px;
    }

    ul.staff-members {
        display: flex;
        align-items: center;
        justify-content: space-evenly;
        margin-bottom: 100px;
        flex-wrap: wrap;

        .staff-member {
            margin-bottom: 30px;

            .column-img {
                width: 300px;
                position: relative;

                @media only screen and (max-width: 600px) {
                    width: inherit;
                }

                .img-hover {
                    opacity: 0;
                    transition: opacity 1s;
                    position: absolute;
                    top: 0;
                    width: 100%;
                    background-color: #ffffffcc;
                    height: 100%;
                    display: flex;
                    align-items: center;
                    justify-content: center;

                    a {
                        font-family: $second-font;
                        text-decoration: none;
                        padding-bottom: 5px;
                        color: black;
                        border-bottom: 2px solid black;
                        transition: color 1s, border-bottom 1s;

                        &:hover {
                            color: #6d6d6d;
                            border-bottom: 2px solid #6d6d6d;
                        }
                    }

                    &:hover {
                        opacity: 1;
                    }
                }
            }

            .column-info-inner {
                display: flex;
                margin-top: 10px;
                margin-bottom: 5px;

                .staff-name {
                    font-family: $second-font;
                    font-size: 1.7em;
                    margin-right: 10px;
                }

                svg {
                    margin: 0 3px;
                    fill: black;
                    transition: fill 1s;

                    &:hover {
                        fill: #a0a0a0;
                    }
                }
            }

            .staff-position {
                font-family: 'josefin-sans';
                color: grey;
            }

        }
    }
}