#contact {
    p {
        margin-top: 20px;
        margin-bottom: 30px;
    }

    form {
        display: flex;
        flex-direction: column;

        label {
            margin-bottom: 5px;
            font-family: $second-font;
        }

        input,
        textarea {
            margin-bottom: 25px;
            font-family: $body-font;
            padding: 25px 25px;
            background: $blue;
            border: 0;
            color: white;
            font-size: 1em;
        }

        textarea {
            resize: vertical;
        }

        input[type="submit"] {
            background-color: transparent;
            font-size: 1em;
            color: black;
            border: 0;
            transition: background-color 1s, color 1s, border-bottom 1s;
            cursor: pointer;
            font-family: $second-font;
            border-bottom: 3px solid black;


            &:hover {
                background-color: $blue;
                color: white;
                border-bottom: 3px solid $orange;
            }
        }
    }

    .contact-info {
        display: flex;
        flex-direction: column;

        a {
            text-decoration: none;
            font-size: 2em;
            font-family: $second-font;
            color: $orange;

            &+a {
                margin-top: 10px;
            }
        }
    }


    svg.phone {
        position: absolute;
        left: 0;
        bottom: 0;
        width: 1000px;
        height: 1000px;
        z-index: -1;
        fill: #0000000f;
    }

    .address {
        font-family: $second-font;
    }
}