@import 'staff-page';

ul.staff-preview {

    li {
        height: 210px;
    }

    .staff-column {
        display: flex;
        margin-bottom: 30px;

        .column-img {
            margin-right: 20px;

            .img-container {
                height: 200px;
                width: 200px;
                overflow: hidden;

                img {
                    width: 120%;
                }
            }
        }
    }

    h2 a {
        transition: color 1s;

        &:hover {
            color: #a2c1ff;
        }
    }

    .column-info {
        width: 600px;
    }

    .staff-position {
        color: #899fca;
        margin-top: -5px;
    }

}