#job-page {
    button.apply {
        background-color: #050720;
        color: white;
        border: 0;
        padding: 10px 15px;
        border-radius: 3px;
        cursor: pointer;
        transition: background-color 1s;
        margin-top: 20px;

        &:hover {
            background-color: #050720cf;
        }
    }

    li {
        list-style: disc;
        margin-left: 20px;
    }
}