@import url("https://use.typekit.net/zml3ebw.css");

// colours
$yellow: #FAC53C;
$orange: #ffc266;
$blue: #050720;
$light-blue: #c1dbff;

$primary-font: 'dunbar-tall',
sans-serif;
$second-font: "josefin-sans",
sans-serif;
$body-font: -apple-system,
system-ui,
BlinkMacSystemFont,
"Segoe UI",
Roboto,
"Helvetica Neue",
Arial,
sans-serif;

@import 'header';
@import 'index';
@import 'about';
@import 'blog';
@import 'staff';
@import 'jobs';
@import 'post';
@import 'contact';
@import 'footer';

body {
    font-family: $body-font;
}

::selection {
    background: #1d1d1d;
    color: white;
}

a {
    text-decoration: none;
    color: black;
}

img {
    width: 100%;
}

section {
    width: 100%;
    height: 100vh;
    display: flex;
    align-items: center;
    justify-content: center;
    position: relative;
    overflow: hidden;

    @media only screen and (max-width: 768px) {
        height: 100%;
    }

    &.secondary {
        background: $orange;

        h1 {
            z-index: 0;

            &::before {
                z-index: -1;
                background-color: white;
            }
        }
    }
}

.inner-container {
    width: 1000px;
    margin: 100px auto;
    padding: 30px;

    @media only screen and (max-width: 1200px) {
        width: inherit;
    }

    a {
        text-decoration: underline;
    }

    h1 {
        margin-bottom: 30px;
        font-size: 2.5em;
        font-family: $primary-font;
        color: $blue;
        position: relative;
        display: table;

        @media only screen and (max-width: 600px) {
            font-size: 1.5em;
        }

        &::before {
            background-color: $orange;
            height: 8px;
            width: 100%;
            position: absolute;
            content: "";
            bottom: -2px;
            z-index: -1;
            left: -7px;
        }
    }

    h2.alt {
        font-size: 3em;
        font-family: $primary-font;
        margin-bottom: 30px;

        @media only screen and (max-width: 600px) {
            font-size: 1.5em;
        }
    }
}

p {
    line-height: 25px;
    margin-bottom: 15px;
    font-size: 1.1em;
}

li {
    margin-bottom: 20px;
}


.button {
    padding: 11px 20px;
    font-size: 1.2em;
    display: inline-flex;
    border-radius: 3px;
    border: 1px solid $blue;
    transition: background-color 1s, color 1s, border 1s;
    text-decoration: none !important;

    &+.button {
        margin-left: 10px;

        @media only screen and (max-width: 400px) {
            margin-left: 0;
        }
    }

    &.main {
        background-color: $blue;
        color: white;
    }

    &.secondary {
        background-color: transparent;
        color: $blue;
    }

    &:hover {
        background-color: #f9b754;
        color: #ffffff;
        border: 1px solid #bb9f75;
    }
}

h2.about,
h2.posts,
h2.social {
    font-size: 1.5em;
    color: $blue;
    margin-bottom: 20px;
    margin-top: 40px;
    position: relative;
    display: table;
    font-family: $primary-font;

    &::before {
        background-color: $light-blue;
        height: 8px;
        width: 100%;
        position: absolute;
        content: "";
        bottom: -2px;
        z-index: -1;
        left: -7px;
    }
}

.read-more {
    text-decoration: underline;
    color: #899fca;
    transition: color 1s;

    &:hover {
        color: #a2c1ff;
    }
}

.large-side-word {
    z-index: -1;
    position: fixed;
    top: 0;
    right: 30px;
    font-size: 5em;
    writing-mode: vertical-rl;
    bottom: 0;
    display: flex;
    align-items: center;
    justify-content: center;
    font-family: $second-font;
    color: #0000000f;
    text-transform: uppercase;
    -webkit-touch-callout: none;
    /* iOS Safari */
    -webkit-user-select: none;
    /* Safari */
    -khtml-user-select: none;
    /* Konqueror HTML */
    -moz-user-select: none;
    /* Old versions of Firefox */
    -ms-user-select: none;
    /* Internet Explorer/Edge */
    user-select: none;
    /* Non-prefixed version, currently
                                  supported by Chrome, Edge, Opera and Firefox */
}

.large-text {
    position: absolute;
    left: 20px;
    top: 10px;
    font-size: 7em;
    font-family: 'dunbar-tall';
    color: #ffffff36;
    -webkit-touch-callout: none;
    /* iOS Safari */
    -webkit-user-select: none;
    /* Safari */
    -khtml-user-select: none;
    /* Konqueror HTML */
    -moz-user-select: none;
    /* Old versions of Firefox */
    -ms-user-select: none;
    /* Internet Explorer/Edge */
    user-select: none;
    /* Non-prefixed version, currently
                                  supported by Chrome, Edge, Opera and Firefox */

    @media only screen and (max-width: 768px) {
        font-size: 4em;
    }
}

.brands {
    display: flex;
    justify-content: space-evenly;

    @media only screen and (max-width: 768px) {
        flex-wrap: wrap;
    }

    .img-container {
        width: 200px;
        height: 200px;
    }
}

h2 a {
    color: $blue;
    font-size: 1.7em;
    font-family: $primary-font;
    margin-bottom: 9px;
    display: inline-flex;
}