header {
    display: flex;
    align-items: center;
    justify-content: space-between;
    position: fixed;
    padding-bottom: 5px;
    width: 100%;
    z-index: 1;
    top: 0;

    .logo-group {
        display: flex;
        align-items: baseline;

        .logo {
            color: $blue;
            font-size: 2.5em;
            font-family: $primary-font;
            margin-left: 5px;
        }

        .dot {
            background: $orange;
            height: 10px;
            width: 10px;
            border-radius: 50%;
            margin-left: 3px;
        }
    }

    nav {
        margin-right: 10px;

        a {
            color: $blue;
            margin: 0 5px;
            font-size: 0.9em;
            position: relative;
            font-family: $second-font;
            text-transform: uppercase;
        }
    }

    #menu-btn {
        display: none;
        margin-right: 10px;
        margin-top: 10px;
        cursor: pointer;
        z-index: 1;
    }
}

@media only screen and (max-width: 768px) {
    nav {
        display: none;
        position: absolute;
        background: $blue;
        height: 100vh;
        width: 100vw;
        top: 0;
        left: 0;
        flex-direction: column;
        align-items: center;
        justify-content: space-evenly;
        font-size: 2em;

        a {
            color: white !important;
        }
    }

    #menu-btn {
        display: block !important;

        &.opened svg {
            fill: white;
        }

        &.closed svg {
            fill: black;
        }
    }

}