#hero {
    height: 100vh !important;

    .inner-container {
        &::before {
            position: absolute;
            content: "";
            background: #ffc266;
            height: 3px;
            width: 100px;
        }

        h1 {
            font-family: $primary-font;
            color: $blue;
            font-size: 5em;
            margin-bottom: 20px;

            @media only screen and (max-width: 600px) {
                font-size: 3em;
            }

            &::before {
                display: none;
            }
        }

        h2 {
            color: white;
            font-size: 1.5em;
            margin-bottom: 20px;
            line-height: 30px;
        }
    }

    img {
        position: absolute;
        right: 0;
        bottom: 0;
        z-index: -99;
        width: 1000px;
        bottom: 20px;
    }

    .circle {
        background: #7fd6ff;
        width: 100vw;
        height: 100vh;
        position: absolute;
        top: 0;
        left: 0;
        right: 0;
        bottom: 0;
        margin: auto;
        z-index: -99;

        animation: circle 5s ease forwards;
    }

    &.blue {
        background: $blue;
    }
}

@keyframes circle {
    to {
        width: 300px;
        height: 300px;
        border-radius: 50%;
        background: $orange;
    }
}


.boxes {
    display: flex;
    align-items: center;
    justify-content: space-evenly;
    margin: 50px 0;

    @media only screen and (max-width: 992px) {
        flex-direction: column;
    }

    .box {
        width: 330px;
        height: 420px;
        display: flex;
        flex-direction: column;
        justify-content: space-around;
        padding: 20px;

        @media only screen and (max-width: 992px) {
            width: inherit;
            height: inherit;
            margin-bottom: 50px;
        }

        h6 {
            font-family: $second-font;
        }

        h2 {
            font-size: 5em;
            font-family: $primary-font;
        }

        &.wide {
            width: 100%;
            height: 200px;
        }

        &.primary {
            background: #ffc266;
        }

        &.secondary {
            background: #c36262;
        }

        &.white {
            background: white;
            color: $blue;
        }

        &.transparent {
            background: transparent;
            color: $blue;
        }
    }
}

.objects {
    display: flex;
    align-items: center;
    justify-content: space-evenly;
    margin-top: 50px;
    flex-wrap: wrap;

    .column {
        padding: 10px;
        width: 250px;
        height: 250px;
        background: #ffffff1a;
        text-align: center;
        display: flex;
        align-items: center;
        justify-content: center;
        flex-direction: column;
        margin-bottom: 35px;

        p {
            font-size: 1em;
            font-family: $second-font;
        }

        span {
            font-size: 3em;
            color: #ffffff;
            font-family: $second-font;
        }
    }
}