footer {
    background: $blue;
    color: white;
    padding: 1px;
    position: relative;
    overflow: hidden;
    z-index: 0;

    a {
        color: white;
        text-decoration: underline;
        transition: color 1s;

        &:hover {
            color: $orange;
        }
    }

    h2 {
        font-family: 'dunbar-tall';
        font-size: 4em;
        margin-bottom: 20px;
        color: #ffc266;

        @media only screen and (max-width: 768px) {
            font-size: 2em;
        }
    }

    .sm {
        margin: 35px 0;

        a {
            text-decoration: none;

            &+a {
                margin-left: 5px;
            }
        }

        svg {
            width: 30px;
            height: 30px;
            fill: white;
            transition: fill 1s;



            &:hover {
                fill: $orange;
            }
        }
    }

    .copyright {
        margin: 15px 0;
    }

    .circle {
        position: absolute;
        top: -252px;
        right: -172px;
        background: #03051b;
        height: 800px;
        width: 800px;
        border-radius: 50%;
        z-index: -1;
        animation: expanding-circle 5s ease infinite alternate;
    }

    @keyframes expanding-circle {
        from {
            height: 800px;
            width: 800px;
        }

        to {
            height: 500px;
            width: 500px;
        }
    }
}